




























































































import Vue, { PropType } from 'vue';
import Auth from '@/libs/auth';
import { WorkspaceObjectModel } from '@/apis/models/WorkspaceObjectModel';
import { MemberRoleCount } from '@/apis/models/MemberGroupModel';

enum MemberRole{
  STUDENT = 'STUDENT',
  TEACHER = 'TEACHER',
  STUDENT_DUPLICATED = 'STUDENT_DUPLICATED',
  STUDENT_EXEMPTED = 'STUDENT_EXEMPTED'
}

export default Vue.extend({
  components: {
  },
  props: {
    memberGroups: {
      type: Array as () => Array<WorkspaceObjectModel>,
      default: () => ([]),
    },
    owner: {
      type: String,
      default: '',
    },
    memberRoleCount: {
      type: Object as PropType<MemberRoleCount>,
      default: () => ({
      }),
    },
  },
  data() {
    return {
      MemberRole,
      groupNames: [] as string[],
    };
  },
  computed: {
    isLoading(): boolean {
      return this.$store.state.isLoading;
    },
  },
  watch: {
    memberGroups() {
      this.groupNames = [];
      this.setGroupNames();
    },
  },
  async mounted() {
    Auth.loginCheck();
  },
  methods: {
    setGroupNames() {
      this.memberGroups.forEach((memberGroup) => {
        this.groupNames.push(memberGroup.uniqueName);
      });
    },
  },
});
