


















































































import Vue from 'vue';
import SelectMemberGroupModal from '@/components/test_maker/SelectMemberGroupModal.vue';
import MemberGroupDetailsCard from '@/components/workspace/MemberGroupDetailsCard.vue';
import EmptyHint from '@/components/workspace/EmptyHint.vue';
import MemberGroupPreviewCard from '@/components/member_group_maker/MemberGroupPreviewCard.vue';
import Auth from '@/libs/auth';
import { WorkspaceObjectModel } from '@/apis/models/WorkspaceObjectModel';
import { MemberGroupModel, MemberRoleCount } from '@/apis/models/MemberGroupModel';
import MemberGroupApi from '@/apis/MemberGroupApi';
import QuizEventHandler from '@/handlers/QuizEventHandler';

enum MemberRole{
  STUDENT = 'STUDENT',
  TEACHER = 'TEACHER',
  STUDENT_DUPLICATED = 'STUDENT_DUPLICATED',
  STUDENT_EXEMPTED = 'STUDENT_EXEMPTED'
}

export default Vue.extend({
  components: {
    SelectMemberGroupModal,
    MemberGroupPreviewCard,
    MemberGroupDetailsCard,
    EmptyHint,
  },
  data() {
    return {
      MemberRole,
      myMemberGroups: [] as WorkspaceObjectModel[], // 選定的membergroups
      selection: [] as WorkspaceObjectModel[],
      selectModeOpen: false,
      searchString: '',
      shareGroupHidden: true,
      currentYear: new Date().getFullYear(),
      currentMonth: new Date().getMonth() + 1,
      minYear: 2023,
      minMonth: 7,
      years: [] as number[],
      months: [] as number[],
      dateData: {
        year: 0,
        month: 0,
        endDate: 0,
      },
      memberGroupModels: [] as MemberGroupModel[],
      memberCountMaps: [] as Map<string, number>[],
      singleTotalList: [] as number[],
      totalCount: 0,
      sigleMemberCountList: [] as MemberRoleCount[],
      memberRoleCount: {
        totalCount: 0,
        confirmedCount: 0,
        unconfirmedCount: 0,
        sentCount: 0,
        unsentCount: 0,
        chargedCount: 0,
        studentCount: 0,
        teacherCount: 0,
        repeatStudentCount: 0,
        exemptedStudentCount: 0,
      } as MemberRoleCount,
    };
  },
  computed: {
    isLoading(): boolean {
      return this.$store.state.isLoading;
    },
    owner(): string {
      return this.$store.state.profile.username;
    },
    workspaceObjects(): WorkspaceObjectModel[] {
      if (this.myMemberGroups) {
        // nature language sort.
        const fileArr = this.myMemberGroups;
        // eg: ['1', '10', '2'] --sort--> ['1', '2', '10']
        const collator = new Intl.Collator('en', { numeric: true, sensitivity: 'base' });
        return fileArr.sort((a, b) => collator.compare(a.metadata.name.toUpperCase().trim(), b.metadata.name.toUpperCase().trim()));
      }
      return [];
    },
    trimSearchString(): string {
      // 修掉輸入的空白
      const string = this.searchString.trim();

      return string;
    },
    searchList(): WorkspaceObjectModel[] {
      const result = [] as WorkspaceObjectModel[];
      this.workspaceObjects.forEach((element) => {
        let listName = '';
        listName = element.uniqueName;
        const inputName = this.trimSearchString;
        const indexOf = listName.toUpperCase().indexOf(inputName.toUpperCase());
        if (indexOf >= 0) {
          result.push(element);
        }
      });
      return result;
    },
    filteredMethodList(): WorkspaceObjectModel[] {
      if (this.trimSearchString !== '') {
        return this.searchList;
      }
      return this.workspaceObjects;
    },
    selectedDate(): string {
      return `${this.dateData.year} ${this.dateData.month}`;
    },
  },
  watch: {
    selectedDate() {
      this.getInitData();
    },
    singleTotalList() {
      this.totalMembersCounter();
    },
    sigleMemberCountList() {
      this.totalMemberRoleCounter();
    },
  },
  async mounted() {
    Auth.loginCheck();
    this.getYears();
    this.getMonths();
    this.getDays();
  },
  methods: {
    async getInitData() {
      this.resetCounter();
      this.$store.commit('updateLoading', true);
      try {
        const requests = [] as Promise<MemberGroupModel>[];
        const requests2 = [] as Promise<Map<string, number>>[];
        this.myMemberGroups.forEach((memberGroup) => {
          requests.push(MemberGroupApi.getMemberGroup(memberGroup.uuid));
        });
        this.myMemberGroups.forEach((memberGroup) => {
          requests2.push(this.getSentCount(memberGroup.uuid));
        });
        this.memberGroupModels = await Promise.all(requests);
        this.memberCountMaps = await Promise.all(requests2);
        this.singleMemberCounter();
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    async getSentCount(memberGroupId: string) {
      const monthStr = String(this.dateData.month).padStart(2, '0');
      return MemberGroupApi.getSentCount(
        memberGroupId,
        `${this.dateData.year}-${monthStr}-01 00:00:00'`,
        `${this.dateData.year}-${monthStr}-${this.dateData.endDate} 00:00:00'`,
      ).then((result) => QuizEventHandler.getJsMap(result.detail));
    },
    singleMemberCounter() {
      if (this.memberGroupModels.length === this.memberCountMaps.length) {
        for (let index = 0; index < this.memberGroupModels.length; index += 1) {
          const memberGroup = this.memberGroupModels[index];
          const sentCountMap = this.memberCountMaps[index];
          const sigleMemberGroupRoleCount = {
            totalCount: 0,
            confirmedCount: 0,
            unconfirmedCount: 0,
            sentCount: 0,
            unsentCount: 0,
            chargedCount: 0,
            studentCount: 0,
            teacherCount: 0,
            repeatStudentCount: 0,
            exemptedStudentCount: 0,
          } as MemberRoleCount;
          let counter = 0;

          memberGroup.members.forEach((item) => {
            counter += 1;
            sigleMemberGroupRoleCount.totalCount += 1;
            if (item.confirmed) {
              sigleMemberGroupRoleCount.confirmedCount += 1;
            } else {
              sigleMemberGroupRoleCount.unconfirmedCount += 1;
            }

            const sentCount = sentCountMap.get(item.uuid);
            let hasSent = false;
            if (sentCount && sentCount > 0) {
              hasSent = true;
            }

            if (hasSent) {
              sigleMemberGroupRoleCount.sentCount += 1;
              if (item.role === MemberRole.TEACHER) {
                sigleMemberGroupRoleCount.teacherCount += 1;
              } else if (item.role === MemberRole.STUDENT) {
                sigleMemberGroupRoleCount.chargedCount += 1;
                sigleMemberGroupRoleCount.studentCount += 1;
              } else if (item.role === MemberRole.STUDENT_DUPLICATED) {
                sigleMemberGroupRoleCount.repeatStudentCount += 1;
              } else if (item.role === MemberRole.STUDENT_EXEMPTED) {
                sigleMemberGroupRoleCount.exemptedStudentCount += 1;
              }
            } else {
              sigleMemberGroupRoleCount.unsentCount += 1;
            }
          });

          this.singleTotalList.push(counter);
          this.sigleMemberCountList.push(sigleMemberGroupRoleCount);
        }
      }
    },
    totalMembersCounter() {
      this.singleTotalList.forEach((singleTotal) => {
        this.totalCount += singleTotal;
      });
    },
    totalMemberRoleCounter() {
      this.sigleMemberCountList.forEach((data) => {
        this.memberRoleCount.totalCount += data.totalCount;
        this.memberRoleCount.confirmedCount += data.confirmedCount;
        this.memberRoleCount.unconfirmedCount += data.unconfirmedCount;
        this.memberRoleCount.sentCount += data.sentCount;
        this.memberRoleCount.unsentCount += data.unsentCount;
        this.memberRoleCount.chargedCount += data.chargedCount;
        this.memberRoleCount.studentCount += data.studentCount;
        this.memberRoleCount.teacherCount += data.teacherCount;
        this.memberRoleCount.repeatStudentCount += data.repeatStudentCount;
        this.memberRoleCount.exemptedStudentCount += data.exemptedStudentCount;
      });
    },
    resetCounter() {
      this.sigleMemberCountList = []; // 清空

      this.memberRoleCount.totalCount = 0;
      this.memberRoleCount.confirmedCount = 0;
      this.memberRoleCount.unconfirmedCount = 0;
      this.memberRoleCount.sentCount = 0;
      this.memberRoleCount.unsentCount = 0;
      this.memberRoleCount.chargedCount = 0;
      this.memberRoleCount.studentCount = 0;
      this.memberRoleCount.teacherCount = 0;
      this.memberRoleCount.repeatStudentCount = 0;
      this.memberRoleCount.exemptedStudentCount = 0;

      this.singleTotalList = []; // 清空
      this.totalCount = 0; // 清空
    },
    getYears() {
      for (let index = this.minYear; index <= this.currentYear; index += 1) {
        this.years.push(index);
      }
      this.dateData.year = this.currentYear;
    },
    getMonths() { // TODO:2024年的時候需再確認會不會壞掉
      this.months = [];
      this.dateData.month = this.currentMonth;
      if (this.dateData.year === this.minYear && this.currentYear === this.minYear) {
        for (let index = this.minMonth; index <= this.currentMonth; index += 1) {
          this.months.push(index);
        }
      } else if (this.dateData.year === this.minYear && this.currentYear !== this.minYear) {
        for (let index = this.minMonth; index <= 12; index += 1) {
          this.months.push(index);
        }
      } else if (this.dateData.year === this.currentYear) {
        for (let index = 1; index <= this.dateData.month; index += 1) {
          this.months.push(index);
        }
      } else {
        for (let index = 1; index <= 12; index += 1) {
          this.months.push(index);
        }
      }
    },
    getDays() {
      const monthDays = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
      this.dateData.endDate = monthDays[this.dateData.month - 1];
    },
    openSelectMemberGroupModal() {
      this.$modal.show('select-member-group-modal', {
        defaultSelection: this.myMemberGroups,
      });
    },
    updateSelectedMemberGroup(selection: WorkspaceObjectModel[]) {
      if (!this.myMemberGroups) {
        throw new Error('quiz event not ready');
      }
      const collator = new Intl.Collator('en', { numeric: true, sensitivity: 'base' });
      selection.sort((a, b) => collator.compare(a.metadata.name.toUpperCase().trim(), b.metadata.name.toUpperCase().trim()));
      this.myMemberGroups = selection;
      this.getInitData();
    },
  },
});
