






























































































































import Vue, { PropType } from 'vue';
import Auth from '@/libs/auth';
import { MemberGroupModel, MemberModel, MemberRoleCount } from '@/apis/models/MemberGroupModel';

enum MemberRole{
  STUDENT = 'STUDENT',
  TEACHER = 'TEACHER',
  STUDENT_DUPLICATED = 'STUDENT_DUPLICATED',
  STUDENT_EXEMPTED = 'STUDENT_EXEMPTED'
}

export default Vue.extend({
  components: {
  },
  props: {
    index: {
      type: Number,
      default: 0,
    },
    memberGroup: {
      type: Object as PropType<MemberGroupModel>,
      default: null,
    },
    sentCountMap: {
      type: Map,
      default: null,
    },
    sigleMemberCount: {
      type: Object as PropType<MemberRoleCount>,
      default: () => ({
        confirmedCount: 0,
        unconfirmedCount: 0,
        sentCount: 0,
        unsentCount: 0,
        chargedCount: 0,
        studentCount: 0,
        teacherCount: 0,
        repeatStudentCount: 0,
        exemptedStudentCount: 0,
      }),
    },
  },
  data() {
    return {
      MemberRole,
    };
  },
  computed: {
    orderedMembers(): MemberModel[] {
      if (this.memberGroup) {
        let confirmedArr = [] as MemberModel[];
        let unConfirmedArr = [] as MemberModel[];
        this.memberGroup.members.forEach((object) => {
          if (object.confirmed) {
            confirmedArr.push(object);
          } else {
            unConfirmedArr.push(object);
          }
        });
        confirmedArr = this.orderArrayEditor(confirmedArr);
        unConfirmedArr = this.orderArrayEditor(unConfirmedArr);

        const arr = [] as MemberModel[];
        confirmedArr.forEach((element) => {
          arr.push(element);
        });
        unConfirmedArr.forEach((element) => {
          arr.push(element);
        });

        if (arr.length === 0) {
          return this.memberGroup.members;
        }
        return arr;
      }
      return [];
    },
  },
  async mounted() {
    Auth.loginCheck();
  },
  methods: {
    checkMemberBeDuplicated(item: MemberModel) { // 確認這個人是否有被設為重複對象
      let isDuplicated = false;
      if (this.memberGroup) {
        this.memberGroup.members.forEach((member) => {
          if (!isDuplicated) {
            if (member !== item && member.belongsTo === item.uuid) {
              isDuplicated = true;
            }
          }
        });
      }
      return isDuplicated;
    },
    belongsToMemberName(uuid: string) {
      let result = '';
      if (this.memberGroup) {
        this.memberGroup.members.forEach((member) => {
          if (member.uuid === uuid) {
            result = member.name;
          }
        });
      }
      return result;
    },
    orderArrayEditor(array: MemberModel[]) {
      const tempArr = [] as MemberModel[];
      const teacherArr = array.filter((object) => object.role === MemberRole.TEACHER);
      const studentArr = array.filter((object) => object.role === MemberRole.STUDENT);
      const duplicatedStudentArr = array.filter((object) => object.role === MemberRole.STUDENT_DUPLICATED);
      const exemptedStudentArr = array.filter((object) => object.role === MemberRole.STUDENT_EXEMPTED);

      const collator = new Intl.Collator('en', { numeric: true, sensitivity: 'base' });
      const newTeacherArr = teacherArr.sort((a, b) => collator.compare(a.name.toUpperCase().trim(), b.name.toUpperCase().trim()));
      const newStudentArr = studentArr.sort((a, b) => collator.compare(a.name.toUpperCase().trim(), b.name.toUpperCase().trim()));
      const newDuplicatedStudentArr = duplicatedStudentArr.sort((a, b) => collator.compare(a.name.toUpperCase().trim(), b.name.toUpperCase().trim()));
      const newExemptedStudentArr = exemptedStudentArr.sort((a, b) => collator.compare(a.name.toUpperCase().trim(), b.name.toUpperCase().trim()));

      newTeacherArr.forEach((element) => {
        tempArr.push(element);
      });
      newExemptedStudentArr.forEach((element) => {
        tempArr.push(element);
      });
      newStudentArr.forEach((element) => {
        tempArr.push(element);
      });
      newDuplicatedStudentArr.forEach((element) => {
        tempArr.push(element);
      });
      return tempArr;
    },
  },
});
